import { evntBus } from "../../bus";
export default {
  data: () => ({
    items_view: 'card',
    item_group: 'Fizz',
    favourites_view: false,
    items_group: ['Foo', 'Bar', 'Fizz', 'Buzz'],
    items: [],
  }),

  methods: {
      get_items() {
        const vm = this;
        frappe.call({
            method: 'posawesome.posawesome.page.posapp.posapp.get_items',
            args: {

            },
            callback: function(r) {
                if (r.message) {
                    // console.log(r.message);
                    vm.items = r.message;
                }
            }
        });
      },
      add_item(item){
        evntBus.$emit('add_item', item);
      }
  },

  created: function () {
      this.$nextTick(function () {
        this.get_items();
      });
      
  }

}


